html, body {
  -webkit-app-region: no-drag;
  color: var(--outerspace);
  background: radial-gradient(circle at 100% 0, #427bb7 0%, #01071a 50%) 100% 0;
  border: 0;
  margin: 0;
  font-family: MainFont;
  font-weight: 300;
  overflow: hidden;
}

div {
  user-select: none;
  cursor: default;
}

::-webkit-scrollbar {
  background: none;
  width: 0;
}

.clip * {
  transition: none !important;
}

#panel {
  margin: 0;
  overflow: hidden;
}

#panel #bar {
  right: 360px;
}

#panel #bot {
  left: 5px;
  right: 360px;
}

#panel #drop {
  border-radius: 3px;
